import Vue from 'vue'
import VueRouter from 'vue-router'


const home=()=>import("../components/content/home/home.vue");
const category=()=>import("../components/content/category/category.vue");
const shopping=()=>import("../components/content/shopping/shopping.vue");
const self=()=>import("../components/content/self/self.vue");
const detail=()=>import("../components/content/detail/detail.vue");

Vue.use(VueRouter)

const routes = [
 // {
  //   path:"/",
  //   component:home
  // },
  {
    path:"/",
    redirect:"/home"
  },
  {
    path:"/home",
    component:home
  },
  
  {
    path:"/category",
    component:category
  },
  {
    path:"/shopping",
    component:shopping
  },
  {
    path:"/self",
    component:self
  },
  {
    path:"/detail/:id",
    component:detail
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
