import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartlist:[],
    
  },
  mutations: {
    //addgoods:function(state,cs)
    addgoods:function(state,neworder)
    {
      neworder.count=1; 
      neworder.ischosen=false
      state.cartlist.push(neworder);
      },
    addcount:function(state,hadgoods){ 
        hadgoods.count++;
    }
  },
  actions: {
    //异步操作和多种逻辑需要经过action
    //context:{state,commit}
    // showtocart:function(context,cs){

    // }
    showtocart:function(context,neworder){
      //数组的find方法，遍历数组，如果函数条件满足，
      //返回满足条件的元素,这里注意，这里与赋值不一样
      //我们修改这个返回值就是修改数组里的这个元素,
      //3个参数，第一个元素，索引，原数组
     return new Promise((rel,rej)=>{
      let hadgoods=context.state.cartlist.find((value)=>
      value.id==neworder.id
    );
     if(hadgoods)
     {
       context.commit("addcount",hadgoods);
       rel("宝贝数量+1")
     }
     else
     {
       context.commit("addgoods",neworder);
       rel("宝贝添加成功")
     }
     })
    },


  },
  modules: {
  }
})
