<template>
<div class="toast" v-if="isshow">
{{message}}
</div>   
</template>

<script>
export default {
    data:function(){
         return{
            isshow:false,
            message:"",
         }
    },
    methods:{
        toast:function(words="哈哈哈",time=1000){
            this.isshow=true;
            this.message=words;
            setTimeout(()=>{ 
              this.isshow=false;
              },time)
         }
    },

}
</script>

<style scoped>
.toast{
    position: absolute;
    left:50%;
    top:50%;
    transform: translateX(-50%) translateY(-50%);
    background-color: rgba(0,0,0,.5);
    color: white;
    text-align: center;
    padding: 5px 8px;
    z-index: 99999;
}
</style>

