<template>
<div :style="activestyle" class="item" @click="topath">
<slot name="imgfont"></slot>
<slot name="fontname"></slot>
</div>
</template>

<script>
export default {
  components:{

},
props:{
  path:{
    type:String,
   },
    activecolor:{
      type:String,
      default:"rgb(250, 151, 168)"
    } 
},
methods:{
  topath:function(){
   if(this.path)
   {
//为了防止重复添加路由造成的报错，我们在这里做一个判断，如果处于活跃就不添加
     if(!this.isactive)  
     
      // this.$router.push(this.path) 
      this.$router.replace(this.path)
      //这里不需要生成历史记录 
      //this.router拿到的是当前路由
   }
  },
    
},
computed:{
  //定义一个函数判断元素是否处于活跃状态
  isactive:function(){
    return this.$route.path.indexOf(this.path)!=-1
    //this.$route拿到的是活跃的路由，或者说是router-link
    //标签里的to属性值
  },
   activestyle:function(){
    if(this.isactive){
      return {color:this.activecolor}
    }
  }
        
  
}
}
</script>

<style  scoped>


.item{
  text-align: center;
  
}


</style>
